/* styles/globals.css - 深色主题 */
:root{
    --bg-900:#050507;
      --bg-800:#0a0b0c;
      --panel: rgba(255,255,255,0.03);
      --muted: #9aa6b2;
      --card:#0e1012;
      --accent1: #ff6b9a; /* pink */
      --accent2: #6ee7f2; /* cyan */
      --accent-grad: linear-gradient(90deg,var(--accent1),var(--accent2));
      --radius: 12px;
      --max-w: 1280px;
      --glass: rgba(255,255,255,0.02);
      --focus: rgba(110,231,242,0.14);
  }
  
  /* reset */
  *{box-sizing:border-box;margin:0;padding:0}
  html,body,#__next {height:100%}
  body{
    background:linear-gradient(180deg,var(--bg) 0%, var(--bg-2) 100%);
    color:var(--text);
    font-family: Inter, "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
    line-height:1.45;
    overflow-y: auto;
  }
  
  /* container helper */
  .container, .container-grid { max-width: var(--container); margin: 20px auto; padding: 0 16px; }
  
  /* header/footer */
  .site-header{border-bottom:1px solid rgba(255,255,255,0.04); background:transparent; padding:20px 0}
  .site-title{font-size:20px; font-weight:600; color:var(--text)}
  
  /* layout grid */
  .shop-wrapper.container-grid {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: var(--gap);
    align-items:start;
  }
  
  /* filters */
  .filters, .filters-area { background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); padding: 16px; border-radius: var(--radius); border: 1px solid rgba(255,255,255,0.03); }
  .label-inline{display:block; font-size:13px; color:var(--muted); margin-bottom:12px;}
  .range-inputs{display:flex; gap:8px; align-items:center; margin-top:8px}
  .range-inputs input{flex:1; background:transparent; color:var(--text); border:1px solid rgba(255,255,255,0.04); padding:8px 10px; border-radius:8px}
  .sep{color:var(--muted); margin:0 6px}
  .label-block{display:block; margin-bottom:12px; color:var(--muted)}
  label select{width:100%; margin-top:8px; padding:8px; border-radius:8px; border:1px solid rgba(255,255,255,0.04); background:transparent; color:var(--text)}
  
  /* button */
  .btn-primary{background:linear-gradient(180deg,var(--accent),var(--accent-2)); color:#04263a; padding:8px 12px; border-radius:8px; border:none; cursor:pointer; font-weight:600}
  .controls-actions{margin-top:8px}
  
  /* product area */
  .product-area{min-height:260px}
  .product-grid{
    display:grid;
    gap:var(--gap);
    grid-template-columns: repeat(3, 1fr);
    margin-bottom:16px;
  }
  
  /* card */
  .product-card{
    background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
    border: 1px solid rgba(255,255,255,0.03);
    padding:12px;
    border-radius:12px;
    display:flex;
    flex-direction:column;
    gap:8px;
    min-height:220px;
  }
  .product-img{width:100%;height:150px;-o-object-fit:cover;object-fit:cover;border-radius:8px;background:linear-gradient(90deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01))}
  .product-title{font-size:15px; color:var(--text); margin:0}
  .product-price{margin-top:auto; color:var(--accent); font-weight:700}
  
  /* pagination */
  .pagination{display:flex; gap:8px; flex-wrap:wrap}
  .page-btn{
    padding:6px 10px;
    border-radius:8px;
    background:transparent;
    border:1px solid rgba(255,255,255,0.03);
    color:var(--muted);
    cursor:pointer;
  }
  .page-btn.active, .page-btn[aria-current="page"]{
    background:linear-gradient(180deg,var(--accent),var(--accent-2));
    color:#04263a;
    border-color:transparent;
  }
  
  /* small screens */
  @media (max-width: 1000px){
    .shop-wrapper.container-grid{ grid-template-columns: 1fr; }
    .product-grid{ grid-template-columns: repeat(2,1fr); }
  }
  @media (max-width: 480px){
    .product-grid{ grid-template-columns: 1fr; }
    .filters { position: sticky; top: 12px; z-index: 10; }
  }
  
  /* accessibility focus */
  :focus{outline: 3px solid rgba(86,182,255,0.12); outline-offset:2px}